@import url(https://fonts.googleapis.com/css?family=Chakra+Petch:300,300italic,regular,italic,500,500italic,600,600italic,700,700italic);

:root {
  --gray: #888;
}

::selection {
  background: rgba(0, 0, 0, 0.2);
  color: white;
  text-shadow: 0px 0px 8px rgba(0, 0, 0, 0.5);
}

body {
  margin: 0;
  font-family: "Chakra Petch", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: white;
  background-image: radial-gradient(var(--gray) 1px, transparent 0);
  background-size: 20px 20px;
  background-position: -19px -19px;
  display: flex;
  justify-content: center;
}

body * {
  margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  padding: 40px;
  max-width: 600px;
}

.glass {
  background: rgba(255, 255, 255, 0.4);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(1px);
  -webkit-backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 1);
}

.filled {
  background-color: white;
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 1);
}

.flex {
  display: flex;
  align-items: center;
  gap: 20px;
}

.navbar {
  padding: 10px;
  justify-content: center;

  h1 {
    font-size: 40px;
  }

  p {
    text-shadow: #fff 0px 0px 10px;
  }
}

.content {
  /* margin-top: 10px; */
}

.card {
  padding: 40px;
  justify-content: center;
  text-align: center;
  flex-direction: column;
}

.btn-primary {
  border-radius: 40px;
  padding: 10px 20px;
  background-color: black;
  color: white;
  border: 1px solid black;
  font-family: inherit;
  font-size: 14px;
  transition: .3s;
  text-decoration: none;
}

.btn-primary:hover {
  background-color: white;
  color: black;
  font-weight: 500;
  letter-spacing: 3px;
  cursor: pointer;
}

.sidebar {
  padding: 20px;
  justify-content: center;

  svg {
    font-size: 40px;
  }

  a {
    text-decoration: none;
    color: inherit;
  }
}

.btn-bar {
  background: linear-gradient(to right, black, black);
  background-size: 100% 2px;
  background-repeat: no-repeat;
  background-position: 100% 100%;
  transition: .3s;
}

.btn-bar:hover {
  color: white;
  background-size: 100% 100%;
}

.btn-bar.disabled {
  color: #ddd;
  background: none;
}

.questions {
  padding-block: 20px;
  justify-content: center;
  text-align: center;
  flex-direction: column;
}

.questions button {
  width: 100%;
  padding: 10px 20px;
  font-weight: 700;
  border: 1px solid black;
  font-family: inherit;
  background-color: black;
  color: white;
  font-size: 14px;
  border-radius: 20px;
  transition: .3s;
}

.questions button:hover {
  width: 100%;
  padding: 10px 20px;
  font-weight: 700;
  /* border: 1px solid; */
  font-family: inherit;
  background-color: white;
  color: black;
  font-size: 14px;
  cursor: pointer;
}

.footer {
  /* position: fixed;
  left: 0;
  bottom: 20px; */
  width: 100%;
  justify-content: center;
  text-align: center;
  padding-inline: 0;
  padding-top: 20px;
}

.socials {
  a {
    text-decoration: none;
    color: inherit;
    font-size: 30px;
    justify-content: center;
    padding-inline: 8px;
  }

  svg {
    margin-top: 10px;
  }
}

/* HTML: <div class="loader"></div> */
.loader {
  width: 40px;
  aspect-ratio: 1;
  border-radius: 50%;
  background: #f03355;
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  animation: l1 2s infinite cubic-bezier(0.3, 1, 0, 1);
}

@keyframes l1 {
  33% {
    border-radius: 0;
    background: #514b82;
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%)
  }

  66% {
    border-radius: 0;
    background: #ffa516;
    clip-path: polygon(50% 0, 50% 0, 100% 100%, 0 100%)
  }
}

/* HTML: <div class="loader"></div> */
.loader2 {
  width: 80px;
  aspect-ratio: 1;
  border: 10px solid #fff;
  box-sizing: border-box;
  background:
    radial-gradient(farthest-side, #000 98%, #fff0) top,
    radial-gradient(farthest-side, #000 98%, #fff0) top,
    radial-gradient(farthest-side, #000 98%, #fff0) left,
    radial-gradient(farthest-side, #000 98%, #fff0) right,
    radial-gradient(farthest-side, #000 98%, #fff0) bottom,
    #fff;
  background-size: 20px 20px;
  background-repeat: no-repeat;
  filter: blur(4px) contrast(10);
  animation: l19 2s infinite;
}

@keyframes l19 {
  0% {
    background-position: top, top, left, right, bottom
  }

  25% {
    background-position: right, top, left, right, bottom
  }

  50% {
    background-position: bottom, top, left, right, bottom
  }

  75% {
    background-position: left, top, left, right, bottom
  }

  100% {
    background-position: top, top, left, right, bottom
  }
}